.reviews__form {
  display: flex;
  flex-direction: column;

  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 73px 0 30px;
    overflow: hidden;
    width: 84.5%;
    margin: 0 auto;
  }

  @media (min-width: $desktop-width) {
    padding: 39px 0 0;
    grid-template-columns: 493px 1fr;
    width: 80%;
  }
}

.reviews__fieldset {
  position: relative;
  margin: 0;
  border: none;
  padding: 0 0 22px;
  display: flex;
  flex-direction: column;

  @media (min-width: $tablet-width) {
    padding: 0 0 47px;
  }

  @media (min-width: $desktop-width) {
    padding: 0 0 51px;
  }

  &--space-bottom-l {
    padding: 0 0 58px;

    @media (min-width: $tablet-width) {
      grid-column: 1/-1;
      padding: 0 0 48px;
    }
  }

  &--space-bottom-m {
    padding: 0 0 17px;

    @media (min-width: $tablet-width) {
      padding: 0 0 32px;
    }
  }

  &--space-bottom-s {
    padding: 0 0 9px;

    @media (min-width: $tablet-width) {
      padding: 0 0 32px;
    }

    @media (min-width: $desktop-width) {
      padding: 0 0 32px;
    }
  }

  &--max-width {
    @media (min-width: $tablet-width) {
      grid-column: 1/-1;
    }
  }
}

.reviews__fieldset--decor::before {
  @media (min-width: $tablet-width) {
    content: "";
    position: absolute;
    right: 0;
    top: -53px;
    bottom: 23px;
    width: 1px;
    height: auto;
    background-color: $linegrey;
  }
}

.reviews__legend {
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
  margin: 0 0 20px;
  color: $black;
  padding: 25px 19px 0;
  width: 100%;
  border-top: 1px solid $grey;

  @media (min-width: $tablet-width) {
    border-top: none;
    margin: 0 0 28px;
    font-size: 21px;
    line-height: 26px;
    padding: 0 19px 0;
  }

  @media (min-width: $tablet-width) {
    max-width: 379px;
  }

  &--space-top {
    padding: 22px 19px 0;

    @media (min-width: $tablet-width) {
      padding: 0 19px 0;
    }
  }
}

.reviews__legend--decor {
  position: relative;
  z-index: 3;
}

.reviews__legend--decor::before {
  @media (min-width: $tablet-width) {
    position: absolute;
    content: "";
    top: 49%;
    right: calc(100% + 11px);
    height: 1px;
    background-color: $linegrey;
    z-index: 2;
    width: 100%;
  }
}

.reviews__legend--decor::after {
  @media (min-width: $tablet-width) {
    position: absolute;
    content: "";
    top: 49%;
    left: calc(100% + 10px);
    height: 1px;
    background-color: $linegrey;
    z-index: 2;
    width: 100%;
  }
}

.reviews__list {
  margin: 0;
  list-style-type: none;
  padding: 0 20px;

  @media (min-width: $tablet-width) {
    padding: 0;
  }

  &--row {
    @media (min-width: $tablet-width) {
      display: grid;
      grid-template-columns: 1fr 1fr;
      align-items: center;
    }

    @media (min-width: $desktop-width) {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
    }
  }

  &--radio {
    @media (min-width: $tablet-width) {
      padding: 0 69px 0 37px;
      column-gap: 50px;
    }

    @media (min-width: $desktop-width) {
      padding: 17px 53px 25px;
    }
  }

  &--checkbox {
    @media (min-width: $tablet-width) {
      padding: 0 84px;
      column-gap: 31px;
    }

    @media (min-width: $desktop-width) {
      padding: 31px 44px 38px;
    }
  }

  &--space-right {
    @media (min-width: $tablet-width) {
      padding: 0 44px 0 0;
    }

    @media (min-width: $desktop-width) {
      padding: 0 63px 3px 0;
      margin: -3px 0 0;
    }
  }

  &--space-left {
    @media (min-width: $tablet-width) {
      padding: 0 0 0 44px;
    }

    @media (min-width: $desktop-width) {
      padding: 0 0 0 64px;
      margin: -3px 0 0;
    }
  }
}

.reviews__item {
  display: flex;
  flex-direction: column;
  margin: 0 0 16px;

  @media (min-width: $desktop-width) {
    margin: 0 0 17px;
  }
}

.reviews__item--checkbox:not(:nth-child(-n+2)) {
  @media (min-width: $tablet-width) {
    margin: 0 0 11px;
  }

  @media (min-width: $desktop-width) {
    margin: 0 0 17px;
  }
}

.reviews__label {
  text-transform: uppercase;
  padding-left: 16px;
}

.reviews__input-container {
  display: flex;
  width: 100%;
}

.reviews__icon {
  position: relative;
  min-width: 58px;
  min-height: 58px;
  background-color: $accentgrey;
}

.reviews__icon-img {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
}

.reviews__label-radio,
.reviews__label-checkbox {
  position: relative;
  cursor: pointer;
  display: flex;
  align-items: center;
  padding-left: 62px;
  align-self: flex-start;
}

.reviews__input-radio:hover + .reviews__control-mark {
  background-color: $grey;
}

.reviews__input-radio:active + .reviews__control-mark {
  background-color: $darkgrey;
}

.reviews__input-radio:focus + .reviews__control-mark {
  outline: 2px solid $darkblue;
}

.reviews__label-radio .reviews__control-mark {
  border-radius: 50%;
}

.reviews__input-radio:disabled + .reviews__control-mark {
  background-color: rgba($lightgrey, 0.8);
  pointer-events: none;
}

.reviews__control-mark {
  position: absolute;
  width: 46px;
  height: 46px;
  background-color: $lightgrey;
  left: 0;
  margin-right: 16px;
  border-radius: 4px;
}

.reviews__input-radio:checked + .reviews__control-mark::after {
  content: "";
  position: absolute;
  width: 16px;
  height: 16px;
  background-color: $baseblack;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  border-radius: 50%;
}

.reviews__input-radio:checked:active + .reviews__control-mark::after {
  opacity: 0.3;
}

.reviews__input-radio:checked:disabled + .reviews__control-mark::after {
  opacity: 0.3;
}

.reviews__input-radio:disabled ~ .reviews__control-label {
  opacity: 0.3;
  pointer-events: none;
}

.reviews__control-label {
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  color: $black;
  padding: 10px 0;
}

.reviews__input-checkbox:checked + .reviews__control-mark::after {
  content: "";
  position: absolute;
  background-image: url("../img/form/checked.svg");
  background-repeat: no-repeat;
  background-position: center;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.reviews__input-checkbox:checked:active + .reviews__control-mark::after {
  opacity: 0.3;
}

.reviews__input-checkbox:hover + .reviews__control-mark {
  background-color: $grey;
}

.reviews__input-checkbox:active + .reviews__control-mark {
  background-color: $darkgrey;
}

.reviews__input-checkbox:focus + .reviews__control-mark {
  outline: 2px solid $darkblue;
}

.reviews__input-checkbox:disabled + .reviews__control-mark {
  background-color: rgba($lightgrey, 0.8);
  pointer-events: none;
}

.reviews__input-checkbox:disabled ~ .reviews__control-label {
  opacity: 0.3;
  pointer-events: none;
}

.reviews__input-checkbox:checked:disabled + .reviews__control-mark::after {
  opacity: 0.3;
}

.reviews__textarea {
  font-family: $main-font;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  color: rgba($black, 0.3);
  padding: 16px 16px 27px;
  border: 0;
  background-color: $lightgrey;
  margin: 0 20px;
  width: auto;
  resize: none;

  &:focus {
    outline: 2px solid $darkblue;
  }

  &:not(:placeholder-shown) {
    color: $black;
  }

  &:disabled {
    background-color: rgba($lightgrey, 0.8);
    pointer-events: none;
    color: rgba($black, 0.1);
  }

  &:disabled::placeholder {
    color: rgba($black, 0.1);
  }

  @media (min-width: $tablet-width) {
    margin: 0;
    padding: 17px 16px 14px;
    min-height: 160px;
  }

  @media (min-width: $tablet-width) {
    margin: 0 auto;
    padding: 16px 16px 14px;
    width: 100%;
  }

  @media (min-width: $desktop-width) {
    max-width: 768px;
  }
}

.reviews__button {
  margin: 0 20px;

  @media (min-width: $tablet-width) {
    margin: 0 0 41px;
    min-width: 324px;
  }

  @media (min-width: $desktop-width) {
    max-width: 384px;
    width: 100%;
    margin: 0 0 41px 96px;
  }
}

.reviews__smalllegend {
  font-size: 12px;
  line-height: 21px;
  text-transform: uppercase;
  color: rgba($baseblack, 0.6);
  align-self: flex-end;
  margin: 3px 20px 24px 0;

  @media (min-width: $tablet-width) {
    margin: 0 0 41px auto;
    padding: 0 0 14px 20px;
    font-size: 14px;
  }

  @media (min-width: $desktop-width) {
    margin: 0 96px 41px auto;
  }
}
