*,
*::after,
*::before {
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

body {
  margin: 0;
}

@font-face {
  font-family: "PTsans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PTsans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-display: swap;
}
.page {
  max-width: 1280px;
  background-color: #f2f2f2;
  height: 100%;
}
@media (min-width: 1280px) {
  .page {
    margin: 0 auto;
    padding: 0 40px;
  }
}

.page__body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  font-family: "PTSans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  color: #333333;
  box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
}

.main-header {
  min-height: 56px;
  background-color: #ffffff;
}

.main-header__logo {
  position: absolute;
  width: 185px;
  display: block;
  text-align: center;
  left: 50%;
  transform: translateX(-50%);
}
@media (min-width: 768px) {
  .main-header__logo {
    z-index: 2;
    width: auto;
  }
}
.main-header__logo:hover {
  opacity: 0.9;
}
.main-header__logo:active {
  opacity: 0.6;
}
.main-header__logo:focus {
  outline: 2px solid #669ec0;
}

.main-nav {
  position: relative;
}

.main-nav__toggle {
  position: absolute;
  right: 20px;
  top: 17px;
  width: 22px;
  height: 22px;
  background-color: transparent;
  cursor: pointer;
  border: none;
  padding: 0;
  z-index: 3;
}

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 22px;
  height: 4px;
  background-color: #81b3d2;
  box-shadow: 0 9px 0 0 #81b3d2, 0 18px 0 0 #81b3d2;
}
@media (min-width: 768px) {
  .main-nav--closed .main-nav__toggle::before {
    display: none;
  }
}

.main-nav--closed .main-nav__toggle:hover {
  opacity: 0.6;
}

.main-nav--closed .main-nav__toggle:active {
  opacity: 0.3;
}

.main-nav--closed .main-nav__toggle:focus {
  outline: 2px solid #669ec0;
}
@media (min-width: 768px) {
  .main-nav--closed .main-nav__toggle:focus {
    outline: none;
  }
}

@media (max-width: 767px) {
  .main-nav--closed .main-nav__list {
    display: none;
  }
}
.main-nav--open .main-nav__toggle::before,
.main-nav--open .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 10px;
  left: -2px;
  width: 28px;
  height: 4px;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .main-nav--open .main-nav__toggle::before,
.main-nav--open .main-nav__toggle::after {
    display: none;
  }
}

.main-nav--open .main-nav__toggle::before {
  transform: rotate(45deg);
}

.main-nav--open .main-nav__toggle::after {
  transform: rotate(-45deg);
}

.main-nav--open .main-nav__toggle:hover {
  opacity: 0.6;
}

.main-nav--open .main-nav__toggle:active {
  opacity: 0.3;
}

.main-nav--open .main-nav__toggle:focus {
  outline: 2px solid #669ec0;
}

.main-nav--open .main-nav__list {
  position: absolute;
  width: 100%;
}

.main-nav--nojs .main-nav__list {
  position: static;
  width: 100%;
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-direction: column;
  position: relative;
  background-color: #81b3d2;
}
@media (min-width: 768px) {
  .main-nav__list {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    background-color: #ffffff;
  }
}

.main-nav__list::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 7px;
  left: 0;
  bottom: 0;
  background-image: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
}
@media (min-width: 768px) {
  .main-nav__list::before {
