  font-family: "PTSans", "Arial", sans-serif;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  padding: 12px;
  background-color: #f2f2f2;
  border: 4px solid transparent;
  width: 100%;
}
.input:hover {
  background-color: #e5e5e5;
}
.input:focus {
  box-shadow: 0 0 0 2px #669ec0;
  outline: none;
  z-index: 1;
}
.input:active {
  background-color: #ffffff;
  border: 4px solid #e5e5e5;
  box-shadow: none;
}
.input:disabled {
  background-color: rgba(242, 242, 242, 0.8);
  pointer-events: none;
}
.input:not(:placeholder-shown) {
  color: #000000;
}
.input::placeholder {
  color: rgba(0, 0, 0, 0.3);
}
.input:disabled::placeholder {
  opacity: 0.3;
}

.modal-container {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 10;
  display: none;
}

.modal-container--open {
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-container__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding: 44px 42px 37px 41px;
  background-color: #ffffff;
  box-shadow: 0 7px 15px rgba(0, 1, 1, 0.15);
}
@media (min-width: 768px) {
  .modal-container__wrapper {
    max-width: 400px;
    padding: 44px 42px 52px 41px;
  }
}
.modal-container__wrapper--size-s {
  padding: 41px 0 0;
}
@media (min-width: 768px) {
  .modal-container__wrapper--size-s {
    max-width: 640px;
    padding: 43px 0 0;
  }
}

.modal-container__title {
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  color: #000000;
  margin: 0 0 25px;
  text-transform: uppercase;
  text-align: center;
}
@media (min-width: 768px) {
  .modal-container__title {
    font-size: 30px;
    line-height: 40px;
    margin: 0 auto 24px;
    max-width: 249px;
  }
}
.modal-container__title--size-s {
  padding: 0 39px 0 37px;
  margin: 0 auto 24px;
  max-width: 444px;
}

.modal-container__text {
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  color: #333333;
  margin: 0 0 28px;
  text-transform: uppercase;
  text-align: center;
}
@media (min-width: 768px) {
  .modal-container__text {
    margin: 0 0 45px;
  }
}
.modal-container__text--space {
  padding: 0 39px 22px 37px;
}
@media (min-width: 768px) {
  .modal-container__text--space {
    margin: 0 0 30px;
  }
}

.modal-container__wrapper-button {
  padding: 20px;
  background-color: #f2f2f2;
  width: 100%;
}
@media (min-width: 768px) {
  .modal-container__wrapper-button {
    padding: 45px 0 42px;
  }
}

.modal-container__button {
  width: 76%;
  font-size: 18px;
}
@media (min-width: 768px) {
  .modal-container__button {
    width: 272px;
    font-size: 21px;
  }
